<template>
	<view class="">
		<view class="content">
			<image class="img" src="../../static/images/touxiang__2.png" mode=""></image>
			<image class="imgs_bar" :src="userIndo.avatarUrl" mode=""></image>
			<text style="margin-top: -50rpx; margin-left: 38rpx; font-size: 36rpx; color: #333333; font-weight: 700;">{{userIndo.nickName}}</text>
		</view>
		<view class="top" @click="clear">
			清理缓存
		</view>
		<view class="bot" @click="singOut">
			退出登录
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userIndo:''//用户信息
			}
		},
		methods: {
			clear() {
				wx.clearStorage({
					success:function () {
						uni.showToast({
							title:"清除成功"
						})
					}
				})
			},
			singOut() {
				wx.openSetting()
			}
		},
		onShow() {
			const userInfo=uni.getStorageSync('userInfo')
			if(userInfo) {
				console.log(userInfo)
				 this.userIndo=userInfo
			}else {
				uni.redirectTo({
					url:'../login/login',
					success() {
						uni.navigateBack({
							delta:1
						})
					}
				})
			}
		}
	}
</script>
<style>
	.content {
		display: flex;
		align-items: center;
		padding-left: 60rpx;
		margin-top: 75rpx;
		position: relative;
	}

	.img {
		height: 198rpx;
		width: 150rpx;
	}
	.imgs_bar{
		height: 120rpx;
		width: 120rpx;
		border-radius: 50%;
		position: absolute;
		top: 20rpx;
		left: 80rpx;
		z-index: 999;
		
	}
	.top{
		font-size: 36rpx;
		height:172rpx ;
		width: 543rpx;
		background-size: 100% 100%;
		margin: 0 auto;
		color: #FFFFFF;
		text-align: center;
		line-height: 172rpx;
		margin-left: 110rpx;
		margin-top: -35rpx;
		background-image: url(data:image/png;base64,);
	}
	.bot{
		position: fixed;
		bottom: 11rpx;
		right: 98rpx;
		font-size: 36rpx;
		height:172rpx ;
		width: 543rpx;
		color: #FFFFFF;
		background-size: 100% 100%;
		margin: 0 auto;
		text-align: center;
		line-height: 172rpx;
		background-image: url(data:image/png;base64,);
	}
</style>
